.form-design {
  margin-top: 120px;
  padding: 30px;
  box-shadow: -6px 11px 5px -4px rgba(0, 0, 0, 0.42);
  -webkit-box-shadow: -6px 11px 5px -4px rgba(0, 0, 0, 0.42);
  -moz-box-shadow: -6px 11px 5px -4px rgba(0, 0, 0, 0.42);
}


.btnContact:hover {
  transition-duration: 1s;
  background-color: #F7CD2E;
}

.btnContact {
  width: 100%;
  border: none;
  border-radius: 1rem;
  padding: 10px;
  background: black;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  margin-top: 20px;
}

input.form-control {
  padding: 10px;
  height: 50px;
  border-radius: 10px;
  margin-top: 20px;
}

.form-bottom-title p{
  text-decoration: none;
  color: black;
  list-style: none;
  margin-top: 15px;
}

